import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import Form from "muicss/lib/react/form";
import Input from "muicss/lib/react/input";
import Button from "muicss/lib/react/button";
import { Link } from "react-router-dom";
import { forgotPassword } from "../../actions/authenticationActions";

export default function ForgotPasswordPage() {
  const dispatch = useDispatch();

  const msg = useSelector(
    (state) => state.authentication.forgotPassword.message
  );

  const [username, setUsername] = useState("");
  const [message, setMessage] = useState(msg || "");

  useEffect(() => {
    setMessage(msg);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [msg]);

  const handleSubmit = (e) => {
    e.preventDefault();
    setMessage("attempting to send email....");
    dispatch(forgotPassword(username.trim()));
  };
  return (
    <div className="account-activation-page">
      <div className="account-activation-logo">
        <Link to="/">
          {/* <img
            id="logo"
            src={"/images/gparency-white-logo.png"}
            alt="GPARENCYLogo"
          /> */}
        </Link>
      </div>
      <div className="account-activation-panel panel">
        <p className="account-activation-header">Forgot Password</p>
        <p className="accent-text-dark">
          Enter your email and we will send you instructions to reset your
          password
        </p>
        <Form onSubmit={(e) => handleSubmit(e)}>
          <Input
            autoFocus
            onChange={(e) => setUsername(e.target.value)}
            label="Email Address"
            floatingLabel={true}
            required={true}
          />
          <p></p>
          <Button type="submit" value="submit">
            Send
          </Button>
          <div id="message">{message}</div>
        </Form>
        <div className="account-activation-panel-footer text-center">
          <Link to="/login" className="xs-text orange-hover accent-text-light">
            Back to Login
          </Link>
        </div>
      </div>
    </div>
  );
}
